import { Box, Skeleton, Slider, SxProps, Theme } from "@mui/material";
import { useState, useEffect, useMemo } from "react";
import { ChartData } from "../../abstractions/chart-data";
import { getChartData } from "../../business-logic/chart-data-converter";
import {
  generatePoints,
  generateTracks,
} from "../../business-logic/test-data-generator";
import { useSlider } from "../../hooks/use-slider";
import { RouteChart } from "../route-chart";
import { RoutePlannerSkeleton } from "./route-planner-skeleton";
import { SliderXSx, SliderYSx } from "./styles";

export function RoutePlanner() {
  const [chartData, setChartData] = useState<ChartData>(); // TODO добавить скелетон

  useEffect(() => {
    const countStr = new URLSearchParams(window.location.search).get("count");
    const count = countStr !== null ? parseInt(countStr) : null;

    const points = generatePoints(!count || count < 0 ? 100 : count);
    const tracks = generateTracks(points);

    setTimeout(() => {
      setChartData(getChartData(points, tracks));
    }, 1000);
  }, []);

  const maxWidth = chartData ? chartData.x.slice(-1).pop() ?? 0 : 0;
  const maxHeight = useMemo(
    () => (chartData ? Math.max(...chartData.y) : 0),
    [chartData]
  );

  const [sliderX, onSliderXChange] = useSlider(maxWidth);
  const [sliderY, onSliderYChange] = useSlider(maxHeight);

  return (
    <Box
      sx={{
        display: "grid",
        padding: "20px",
        boxSizing: "border-box",
        gridTemplateAreas: `"vs c"
                            ". hs"`,
        height: "100%",
        gridTemplateColumns: "50px auto",
        gridTemplateRows: "auto 50px",
      }}
    >
      {chartData ? (
        <>
          <Slider
            sx={SliderYSx}
            orientation="vertical"
            value={sliderY}
            onChange={onSliderYChange}
            valueLabelDisplay="auto"
            min={0}
            max={maxHeight}
            size="small"
          />
          <Box
            sx={{
              gridArea: "c",
              height: "100%",
              minHeight: "0",
              minWidth: "0",
            }}
          >
            <RouteChart
              chartData={chartData}
              minX={sliderX[0]}
              maxX={sliderX[1]}
              minY={sliderY[0]}
              maxY={sliderY[1]}
            />
          </Box>
          <Slider
            sx={SliderXSx}
            value={sliderX}
            onChange={onSliderXChange}
            valueLabelDisplay="auto"
            min={0}
            max={maxWidth}
            size="small"
          />
        </>
      ) : (
        <RoutePlannerSkeleton />
      )}
    </Box>
  );
}
